.account
  display: flex
  justify-content: space-between
  align-items: center
  border: 1px solid #ccc
  background-color: #fff
  width: 80%
  margin: 0 auto
  flex-direction: column
  padding: 1.5rem
  box-sizing: border-box
  text-align: left
  margin-bottom: 2rem
  @media (min-width: 720px)
    flex-direction: row
  &__content-wrapper
    width: 100%
    flex: 1

  &__title
    font-size: 1rem
    font-weight: normal

  &__amount
    font-size: 2.5rem
    font-weight: bold

  &__cta
    flex: 0

  &__transaction-button
    display: block
    width: 100%
    padding: 8px
    font-size: 1.1rem
    font-weight: bold
    margin-top: 1rem
    border: none
    background-color: #00bc77
    color: #fff
    cursor: pointer
    @media (min-width: 720px)
      width: 200px
